


































import { Component, Vue } from 'vue-property-decorator';
import ScanSigned from '@/componets/scan_signed.vue';
import {notes, NoteType} from '@/note';

import { Button, Field } from 'vant';
import { Row, Col } from 'vant';

import 'vant/lib/button/style';
import 'vant/lib/field/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';

Vue.use(Row).use(Col);
Vue.use(Button);
Vue.use(Field);


@Component({
  components: { ScanSigned},
})

export default class originalScanSign extends Vue {

  private showConfirm = true;
  private showSign = false;
  private showSigned = false;
  private code = '';
  private sign = '';

  public created(): void {

   }

   public confirmOriginal():void {
    this.showConfirm = false;
    this.showSign = true;
   }

   public signment():void {
    if (this.sign) {
    this.showSign = false;
    this.showSigned = true;
    this.code = this.sign;
   } else {
      notes.add({type: NoteType.Error, message: '请签名！'});

    }
  }

   public cancelSign():void {
    this.showSign = false;
    this.showConfirm = true;
   }

  }
